html, body, .all-wrapper {
  height: 100%; }

.all-wrapper {
  position: relative;
  padding: 100px 0 40px;
  overflow: hidden; }

.site-header {
  position: absolute;
  background: #fff;
  height: 100px;
  width: 100%;
  left: 0;
  top: 0;
  border-bottom: 1px solid #b8bbbe; }

.site-footer {
  position: absolute;
  background: #fff;
  height: 40px;
  bottom: 0;
  left: 0;
  width: 100%;
  border-top: 1px solid #b8bbbe; }

.main-content {
  height: 100%; }

.container {
  max-width: 1480px;
  height: 100%; }

.content-side {
  padding-left: 100px;
  padding-right: 50px;
  margin-right: 335px;
  background: rgba(250, 250, 250, 0.85);
  height: 100%;
  overflow-y: auto; }

.sidebar {
  float: right;
  background: rgba(0, 82, 145, 0.8);
  height: 100%;
  width: 335px;
  overflow-y: auto; }
  .sidebar::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: purple;
    z-index: -1; }

.home .main-content {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top right; }

.site-header {
  padding-left: 100px; }
  .site-header .logo {
    display: block;
    float: left;
    margin-top: 17px; }
    .site-header .logo img {
      max-width: 100%;
      height: auto;
      display: block; }

.site-header__inner {
  float: left;
  position: relative;
  margin-left: 20px;
  padding-right: 22px; }

.site-header__nav {
  float: left; }
  .site-header__nav ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    line-height: 100px; }
    .site-header__nav ul li {
      display: inline-block;
      padding: 0 30px;
      border-right: 1px solid #5c5f66;
      line-height: normal; }
      .site-header__nav ul li a {
        transition: all 0.3s ease-in-out;
        font-family: "OfficinaSansBook";
        color: #5c5f66;
        font-size: 14px;
        text-transform: uppercase;
        text-decoration: none;
        border-bottom: 2px solid transparent;
        padding-bottom: 2px; }
        .site-header__nav ul li a:hover {
          color: #e92d2a;
          border-color: #e92d2a; }
      .site-header__nav ul li.current-menu-item a {
        color: #e92d2a;
        border-color: #e92d2a; }
      .site-header__nav ul li:last-child {
        border: none; }

.search {
  background: url(../img/search.png) center center no-repeat;
  width: 16px;
  height: 42px;
  cursor: pointer;
  float: left;
  margin-top: 30px; }
  .search__inner {
    position: absolute;
    width: 100%;
    height: 42px;
    right: 0;
    top: 50%;
    margin-top: -21px;
    padding-right: 42px;
    display: none;
    background: #fff;
    border-radius: 8px; }
  .search input {
    display: none;
    width: 100%;
    height: 100%;
    border: none;
    background: url(../img/search.png) left 12px center no-repeat #f4f4f6;
    outline: 0;
    border-radius: 8px;
    padding: 0 15px 0 40px;
    font-family: "OfficinaSansBook";
    line-height: 42px; }
  .search.active {
    position: absolute;
    width: 100%;
    background: transparent; }
    .search.active .search__inner {
      display: block; }
    .search.active input {
      display: block; }
    .search.active span {
      position: absolute;
      right: 0;
      top: 0;
      cursor: pointer;
      z-index: 1;
      height: 42px;
      width: 42px; }
      .search.active span::after {
        content: '';
        background-image: url(../img/sprite.png);
        background-position: 0px 0px;
        width: 16px;
        height: 16px;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-top: -8px;
        margin-left: -8px; }
